.app {
  margin: 10px 0px 0px 10px;
}

.h1 {
  font-size: 30px;
}

.select-groups {
  display: flex;
  flex-wrap: wrap;
}

.group {
  border: 1px solid rgba(0,0,0,0.2);
  padding: 5px 5px 5px 5px;
  margin: 0px 5px 5px 0px;
}

.group .select {
  display: inline-block;
}

.group .name {
  display: inline-block;
  font-size: 20px;
  margin: 0px 5px 0px 5px;
  font-style: italic;
}

.group .characters > * {
  display: inline-block;
  margin: 2px 2px 2px 2px;
  padding: 2px 2px 2px 2px;
  font-size: 20px;
}

.character .roman {
  font-size: 0.65em;
  font-style: italic;
  opacity: 0.7;
  text-align: center;
}

.guess {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.guess .toGuess {
  font-size: 100px;
  margin-bottom: 20px;
}

.guess .done-placeholder {
  font-size: 12px;
}

.guess .invisible {
  visibility: hidden;
}
